<template>
  <div>
    <!-- <mu-appbar style="width: 100%;" class="header rankHeader backgroundNaviBar">
      <a @click="()=>{
           $router.go(-1)
          }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">水环境质量评价</span>
    </mu-appbar>-->
    <van-nav-bar
      :fixed="true"
      title="水环境质量评价"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="search" slot="right" /> -->
    </van-nav-bar>
    <div class="fixedRanking backgroundNaviBar">
      <!-- <div class="rankHeader">
        <div @click="()=>{
            $router.go(-1)}">
          <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
        </div>
        <div class="rankingTitle" style="flex:1">
          <span>水环境质量评价</span>
        </div>
      </div>-->

      <div style="color: white;" class="backgroundNaviBar" id="showHeader">
        <div class="selectTimeFast">
          <div @click="Day(3,0)" :style="threeStyle" class="three">
            <span>近三天</span>
          </div>
          <div @click="Day(7,0)" :style="weekStyle" class="week">
            <span>近一周</span>
          </div>
          <div @click="Day(30,0)" :style="monthStyle" class="month">
            <span>近一月</span>
          </div>
        </div>
        <div class="imageSizeTime">
          <div class="flexTime" style="width:100%;" @click="selectStartTime()">
            <span class="leftTime">{{showStartTime}}</span>
            <span>-</span>
            <span class="rightTime">{{showEndTime}}</span>
            <div class="img">
              <img style="margin-left:5px;" src="static/imgs/historyToBottom.png" alt />
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="containerEcharts" :style="margin">
      <div class="containerBack containerBack">
        <div class="heightTitle">
          <div class="titleBtn">
            <span>水质达标情况分析</span>
          </div>
        </div>
        <div id="Analysis" class="echartsHeight"></div>
      </div>
      <div class="containerBack border-radius">
        <div class="heightTitle">
          <div class="titleBtn">
            <span>超标站点数</span>
          </div>
        </div>
        <div id="rates" class="echartsHeight"></div>
      </div>
      <div class="containerBack border-radius">
        <div class="heightTitle">
          <div class="titleBtn">
            <span>超标时段分析</span>
          </div>
        </div>
        <div id="Time" class="echartsHeight"></div>
      </div>
    </div>
    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list @item-click="closeBottomSheet">
          <!-- <mu-list-item>
            <div class="titleselect">
              <span>站点名称：</span>
            </div>
            <div class="select" @click="selectDataPic" id="selectStation">
              <input type="text" v-model="station" placeholder="请选择站点" class="input" disabled />
            </div>
          </mu-list-item>-->
          <mu-list-item>
            <div class="titleselect">
              <span>开始时间：</span>
            </div>
            <div class="select" @click="selectStartTime('start')">
              <input type="text" class="input" disabled placeholder="选择开始时间" v-model="value3" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="titleselect">
              <span>结束时间：</span>
            </div>
            <div class="select" @click="selectStartTime('end')">
              <input type="text" class="input" placeholder="选择结束时间" disabled v-model="value4" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="btnBottom">
              <div class="left" @click="closeBottomSheet()">取消</div>
              <div class="right" :style="'background:'+$store.state.color" @click="confirmData()">确定</div>
            </div>
          </mu-list-item>
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
    <mu-container>
      <mu-bottom-sheet :open.sync="showStartTimeQuery">
        <mu-list @item-click="closeBottomSheet">
          <van-datetime-picker
            v-model="currentDate"
            type="date"
            :min-date="minDate"
            :max-date="maxDate"
            @confirm="confirmTimeque"
            @cancel="closeTime"
          />
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
    <van-overlay :show="showover" @click="showover = false" class="overlay">
      <van-loading type="spinner" color="#1989fa" />
    </van-overlay>
  </div>
</template>
<script>
let Echarts = require("echarts");
import Echart from "../../../static/index";
import AppBace from "../../../static/ajax";
import Vue from "vue";
import { Toast, Overlay, Loading } from "vant";
Vue.use(Toast)
  .use(Overlay)
  .use(Loading);
export default {
  data() {
    return {
      showover:false,
      open: false,
      station: "",
      value3: AppBace.DateTime(7),
      value4: AppBace.filter(new Date()),
      openTime: false,
      showTime: "",
      currentDate: new Date(),
      minDate: new Date("1970-07-01"),
      stationName: "",
      showStartTimeQuery: false,
      showStartTime: "",
      showEndTime: "",
      threeStyle: "",
      weekStyle: "",
      monthStyle: "",
      maxDate: new Date(),
      startx: "",
      starty: "",
      showData: false,
      margin: "margin-top:145px;"
    };
  },
  methods: {
    confirmTimeque(time) {
      this.showStartTimeQuery = false;
      this.value4 = AppBace.filter(time);

      var timeDates = this.timeDates;
      this.Day(timeDates, this.value4);
    },
    selectStartTime() {
      this.showStartTimeQuery = true;
    },
    Day(time, currentDateTime) {
      if (currentDateTime === 0) {
        var timer = AppBace.DateTime(currentDateTime);
        this.value3 = AppBace.DateTime1(time, timer);
        this.value4 = timer;
      } else {
        this.value3 = AppBace.DateTime1(time, currentDateTime);
      }
      switch (time) {
        case 3:
          this.threeStyle = "";
          this.weekStyle = " border:none;color:rgb(221, 230, 249)";
          this.monthStyle = "border:none;color:rgb(221, 230, 249)";
          break;
        case 7:
          this.threeStyle = "border:none;color:rgb(221, 230, 249)";
          this.weekStyle = "";
          this.monthStyle = "border:none;color:rgb(221, 230, 249)";
          break;
        default:
          this.threeStyle = "border:none;color:rgb(221, 230, 249)";
          this.weekStyle = "border:none;color:rgb(221, 230, 249)";
          this.monthStyle = "";
          break;
      }
      this.timeDates = time;
      this.showStartTime = AppBace.filterDatePoint(this.value3);
      this.showEndTime = AppBace.filterDatePoint(this.value4);
      // debugger
      this.initData();
      this.initDataStation();
    },
    // Day(time) {
    //   this.value3 = AppBace.DateTime(time);
    //   this.initData();
    //   this.initDataStation();
    // },
    confirmData(value) {
      this.open = false;
      this.initData();
      this.initDataStation();
    },
    closeTime() {
      this.openTime = false;
    },
    closeBottomSheet() {
      this.open = false;
    },
    confirmTime(value) {
      switch (this.showTime) {
        case "start":
          this.value3 = AppBace.filter(value);
          break;
        default:
          this.value4 = AppBace.filter(value);
          break;
      }
      this.openTime = false;
    },
    closeTime() {
      this.openTime = false;
    },
    // selectStartTime(e) {
    //   this.openTime = true;
    //   this.showTime = e;
    // },
    openBotttomSheet() {
      this.open = true;
    },
    initDataEchart(res) {
      var _this = this;
      var option = Echart.AnalysisOfWater(res);
      let myChart = Echarts.init(document.getElementById("Analysis"));
      myChart.setOption(option);
      myChart.on("click", function(params) {
        // 控制台打印数据的名称
        // console.log(params.name);
        // console.log(_this.$store.state.stateName);
        var namearr = [];
        namearr = _this.$store.state.stateName;
        for (let index = 0; index < namearr.length; index++) {
          var name = namearr[index];
          if (name.slice(0, 5) === params.name) {
            console.log(namearr[index]);
            _this.stationName = namearr[index];
          }
        }
        _this.$router.push({
          path: "/Assistant",
          query: {
            stationName: _this.stationName,
            startTime: _this.value3,
            endTime: _this.value4
          }
        });
      });
    },
    async initData() {
      /**
         * account: "",
          isExcel: "",
          lasttime: "",
          pageNum: 0,
          pageSize: 0,
          reportType: "",
          sortOrder: "",
          starttime: "",
          vehicle: "",
          waterstation: ""
         */
      var url = "/station/water-data/rate";
      var _this = this;
      this.$http
        .post(url, {
          endTime: _this.value4,
          startTime: _this.value3
        })
        .then(res => {
          console.log("1111111", res);
          _this.showover = false;
          Echart.StationRates(res.data.countMap);
          _this.initDataEchart(res.data.data);
        })
        .catch(err => {
          console.log(err);
          if (err.status === 401) {
            Toast("身份已过期请重新登录！");
            _this.$router.replace("/");
          }
        });
      //   var endTime = "2019-10-01";
      //   var startTime = "2019-07-07";
      //   var res = await AppBace.request(
      //     url,
      //     "POST",
      //     {
      //       endTime,
      //       startTime
      //     },
      //     this.token
      //   );
      // console.log(res);
    },
    async initDataStation() {
      var _this = this;
      var url = "/station/water-data/period";

      // var params = {
      //   endTime: _this.value4,
      //   startTime: _this.value3
      // };
      // var starttime = _this.value4;
      // var lasttime = _this.value3;
      // var res = await AppBace.request(url,"POST",{
      //     starttime,lasttime
      // },this.token);
      // console.log(res);
      //  Echart.StationTime(res.data.data);
      this.$http
        .post(url, {
          lasttime: _this.value4,
          starttime: _this.value3
        })
        .then(res => {
          console.log("2222222222", res);
          Echart.StationTime(res.data.data);
        })
        .catch(err => {
          console.log(err);
          if (err.response.status === 401) {
            Toast("身份已过期请重新登录！");
            _this.$router.replace("/");
          }
        });
    },
    initStation() {
      var url = "/station/water-data/period";
      var _this = this;
      this.$http
        .post(url, {
          account: "",
          isExcel: "",
          lasttime: _this.value4,
          pageNum: 1,
          pageSize: 100000,
          reportType: "",
          sortOrder: "",
          starttime: _this.value3,
          vehicle: "VE001",
          waterstation: ""
        })
        .then(res => {
          console.log(res);
          //  Echart.StationTime(res.data.data);
        })
        .catch(err => {
          console.log(err);
        });
    },
    dataScoll() {
      var startx, starty;
      //获得角度

      //根据起点终点返回方向 1向上 2向下 3向左 4向右 0未滑动

      //手指接触屏幕
      window.addEventListener("touchstart", this.startFun, true);
      //手指离开屏幕
      window.addEventListener("touchend", this.endFunc, true);
    },
    getAngle(angx, angy) {
      return (Math.atan2(angy, angx) * 180) / Math.PI;
    },
    startFun(e) {
      this.startx = e.touches[0].pageX;
      this.starty = e.touches[0].pageY;
    },
    endFunc(e) {
      var endx, endy;
      endx = e.changedTouches[0].pageX;
      endy = e.changedTouches[0].pageY;
      var direction = this.getDirection(this.startx, this.starty, endx, endy);
      switch (direction) {
        case 0:
          console.log("未滑动！");
          break;
        case 1:
          // console.log("向上！");
          // this.showHeader = false;
          this.margin = "margin-top:50px";
          // this.showHeaderStyle =
          // "showHeaderStyle:opacity: 0;filter:Alpha(opacity=0); transition: opacity 2s;";
          $("#showHeader").slideUp(200);
          //  $("#showHeader").slideToggle(200);

          break;
        case 2:
          // console.log("向下！");
          // this.showHeader = true;
          //  $("#showHeader").slideToggle(200);
          $("#showHeader").slideDown(200);
          this.margin = "margin-top:145px";
          break;
        case 3:
          console.log("向左！");
          break;
        case 4:
          console.log("向右！");
          break;
        default:
      }
    },
    getDirection(startx, starty, endx, endy) {
      var angx = endx - startx;
      var angy = endy - starty;
      var result = 0;

      //如果滑动距离太短
      if (Math.abs(angx) < 2 && Math.abs(angy) < 2) {
        return result;
      }

      var angle = this.getAngle(angx, angy);
      if (angle >= -135 && angle <= -45) {
        result = 1;
      } else if (angle > 45 && angle < 135) {
        result = 2;
      } else if (
        (angle >= 135 && angle <= 180) ||
        (angle >= -180 && angle < -135)
      ) {
        result = 3;
      } else if (angle >= -45 && angle <= 45) {
        result = 4;
      }

      return result;
    }
    // selectStartTime() {
    //   this.showStartTimeQuery = true;
    // }
  },
  async mounted() {
    // Echart.AnalysisOfWater();
    // $("#showHeader").slideDown(200);
    this.showover = true;
    //手指接触屏幕
    window.addEventListener("touchstart", this.startFun, true);
    //手指离开屏幕
    window.addEventListener("touchend", this.endFunc, true);
    this.token = sessionStorage.getItem("token");
    this.value3 = await AppBace.DateTime(7);
    this.value4 = await AppBace.DateTime(0);
    this.Day(7, this.value4);
    // this.initData();
    // // this.initStation();
    // this.initDataStation();
    // "account": "",
    // "isExcel": "",
    // "lasttime": "",
    // "pageNum": 0,
    // "pageSize": 0,
    // "reportType": "",
    // "sortOrder": "",
    // "starttime": "",
    // "vehicle": "",
    // "waterstation": ""
  },
  destroyed() {
    window.removeEventListener("touchstart", this.startFun, true);
    window.removeEventListener("touchend", this.endFunc, true);
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
@import "../../../static/css/maintain.css";
.fixedRanking {
  /* background-color: #3572fe; */
  color: white;
  position: fixed;
  top: 46px;
  width: 100%;
  z-index: 999;
}
.header {
  position: fixed;
  top: 0;
  width: 100%;
}
.icon img {
  width: 24px;
  height: 24px;
}
#Analysis {
  width: 100%;
  height: 300px;
}
.containerEcharts {
  /* margin-top: 60px; */
  /* margin-top: 160px; */

  padding: 10px;
}
.containerBack {
  background: white;
  /* border-radius: 10px; */
  /* padding: 5px 10px; */
  margin-bottom: 10px;
}
.titleBtn {
  border-left: 3px solid rgb(53, 114, 254);
  padding-left: 5px;
  /* padding-bottom: 3px; */
  /* border-bottom: rgb(245, 245, 245) solid 1px; */
}
.echartsHeight {
  width: 100%;
  height: 300px;
}
#Time {
  height: 400px;
  width: 100%;
}
.heightTitle {
  height: 50px;
  /* border-bottom: 1px solid #ccc; */
  border-bottom: 1px solid rgb(240, 240, 240);
  margin: 0 10px;
  display: flex;
  align-items: center;
}
.three {
  border-radius: 10px;
  /* background-color: white; */
  color: white;
  padding: 5px 10px;
  /* border-bottom: 2px solid rgb(53, 114, 254); */
}
.backgroundTime {
  background-color: white;
  /* border-radius: 10px; */
  height: 100px;
  /* margin-bottom: 10px; */
  /* display: flex; */
  padding: 10px 20px;
  margin-top: 60px;
}
.week {
  border-radius: 10px;
  /* background-color: white; */
  color: white;
  padding: 5px 10px;
  /* border-bottom: 2px solid rgb(53, 114, 254); */
}
.month {
  border-radius: 10px;
  /* background-color: white; */
  color: white;
  padding: 5px 10px;
  /* border-bottom: 2px solid rgb(53, 114, 254); */
}
.backTime {
  border-bottom: 1px #ccc solid;
  padding: 5px;
  display: flex;
  justify-content: space-between;
  /* padding: 5px 10px; */
}
.imageSizeTime img {
  width: 16px;
  height: 16px;
}
.imageSizeTime {
  display: flex;
  /* justify-content: flex-start; */
  text-align: left;
  align-content: center;
  height: 60px;
  width: 100%;
  /* padding: 0 30px;
  padding-bottom: 20px; */
  /* color: white; */
  /* flex-direction: row; */
}
.img {
  display: flex;
  align-items: center;
  height: 20px;
}
.flexTime {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 30%;
  justify-content: start;
  color: white;
  font-size: 14px;
  padding-left: 15px;
}

/* add
 */
.rankHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  padding: 0 10px;
  font-size: 18px;
}
.rankingTitle {
  display: flex;
  justify-content: center;
}
.selectTimeFast {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 40px;
  padding-left: 10px;
}
.selectTimeFast div {
  border: 1px white solid;
  border-radius: 15px;
  padding: 2px 10px;
  font-size: 12px;
  margin-right: 10px;
}
.leftTime {
  margin-right: 10px;
}
.rightTime {
  margin-left: 10px;
}
</style>


